import React from 'react';
import Dropdown from './Dropdown';
import '../styles/MyPage.css';

const Contact = props => {

    const [firstDropdown, setFirstDropdown] = React.useState(false);
    const [secondDropdown, setSecondDropdown] = React.useState(false);


    //  
    const handleCopy = (text) => {
        navigator.clipboard.writeText(text)
        .then(()=> {
            alert('');
        })
        .catch(()=> {
            alert('');
        })
    }
    
 
    return (
        <div className='contact'>
             <p><span className='dot'></span> 00 010-2222-1234</p>
            <button onClick={e => setFirstDropdown(!firstDropdown)}>

                {
                    firstDropdown
                    ? ' '
                    : ' '
                }

            </button>
            <Dropdown visibility={firstDropdown}>
                <ul className='dropdownUl'>
                    <li>oo 1002-222-132344 <span onClick={()=>{handleCopy('1002222132344')}}></span></li>
                    <li>oo 9938-11-025 <span onClick={()=>{handleCopy('993811025')}}></span></li> 
                </ul>
            </Dropdown>
            <br></br>
            <p><span className='dot'></span> 00 010-2222-1234</p>
            <button onClick={e => setSecondDropdown(!secondDropdown)}>

                {
                    secondDropdown
                    ? ' '
                    : ' '
                }

            </button>
            <Dropdown visibility={secondDropdown}>
                <ul className='dropdownUl'>
                    <li>oo 786-99-14564 <span onClick={()=>{handleCopy('7869914564')}}></span></li>
                    <li>oo 3333-5555-4568 <span onClick={()=>{handleCopy('333355554568')}}></span></li> 
                </ul>
            </Dropdown>

        </div>
        
    )
}

 

export default Contact;
